import { Flex, Heading } from "@chakra-ui/react"
import { AnimationControls, motion } from "framer-motion"



type NavbarProps = {
    navbarAnimControls: AnimationControls
}

function Navbar({ navbarAnimControls }: NavbarProps) {
    const navbarVariants = {
        hidden: { x: "100vw" },
        show: {
            x: 0,
            transition: {
                type: "spring",
                duration: 0.5
            }
        }
    }
    // TODO: NO Z INDEX!!!
    return (
        <Flex w="full" position="fixed" top={8} right={20} justifyContent="end" zIndex={100}>
            <motion.div
                style={{display: "flex", gap: 30}}
                variants={navbarVariants}
                initial="hidden"
                animate={navbarAnimControls}
            >
                <a href=""><Heading fontSize={20}>About Me</Heading></a>
                <a href=""><Heading fontSize={20}>Projects</Heading></a>
                <a href=""><Heading fontSize={20}>Skills</Heading></a>
                <a href=""><Heading fontSize={20}>Contact</Heading></a>
            </motion.div>
        </Flex>
    )
}

export default Navbar
