import {
    Box,
  ChakraProvider,
  theme,
} from "@chakra-ui/react"
import { useAnimation } from "framer-motion"
import HeroSection from "./components/HeroSection"
import Navbar from "./components/Navbar"


export const App = () => {
    const navbarAnimControls = useAnimation()
    
    return (
        <ChakraProvider theme={theme}>
            <Box overflow="hidden">
                <Navbar navbarAnimControls={navbarAnimControls} />
                <HeroSection navbarAnimControls={navbarAnimControls} />
            </Box>
        </ChakraProvider>
    )
}


