import { Box, Button, Heading } from "@chakra-ui/react"
import { AnimationControls, motion, useAnimation } from "framer-motion"
import { useState } from "react"
import Typewriter, { TypewriterClass } from "typewriter-effect"


type HeroHeadingProps = {
    navbarAnimControls: AnimationControls
}

function HeroHeading({ navbarAnimControls }: HeroHeadingProps) {
    const actionBtnAnimControls = useAnimation()

    function typewriteHeading(typewriter: TypewriterClass) {
        typewriter
        .pauseFor(400)
        .typeString(`Hi, I'm Vincent. <br />I'm a <span style="color: #63B3ED">web developer.</span>`)
        .start()
        .callFunction(() => {
            actionBtnAnimControls.start("show")
            navbarAnimControls.start("show")
        })
    }

    const headingVariants = {
        hidden: { x: "100vw" },
        show: {
            x: 0,
            transition: {
                type: "spring",
                delay: 0.4,
                duration: 0.6
            },
        }
    }

    const actionBtnVariants = {
        hidden: { opacity: 0, x: "-100vw" },
        show: {
            opacity: 1,
            x: 0,
            transition: {
                type: "spring",
                duration: 0.6
            }
        }
    }
    // TODO: NO PADDING. SHOULD BE MARGIN!!!
    return (
        <Box pt="20rem" pl="20rem">
            <motion.div
                variants={headingVariants}
                initial="hidden"
                animate="show"
            >
                <Heading fontSize={55} order={1}>
                    <Typewriter
                        options={{ delay: 60 }}
                        onInit={typewriteHeading}
                    />
                </Heading>

                <motion.div
                    variants={actionBtnVariants}
                    initial="hidden"
                    animate={actionBtnAnimControls}
                >
                    <Button mt={6} size="lg" bg="whiteAlpha.300">Action</Button>
                </motion.div>
            </motion.div>
        </Box>
    )
}

export default HeroHeading