import { Box, Heading } from "@chakra-ui/react"
import { AnimationControls, motion } from "framer-motion"
import { createRef, useEffect } from "react"
import { ThpaceGL } from 'thpace'
import HeroHeading from "./HeroHeading"


type HeroSectionProps = {
    navbarAnimControls: AnimationControls
}

function HeroSection({ navbarAnimControls }: HeroSectionProps) {
    const bgRef = createRef<HTMLCanvasElement>()
    
    useEffect(() => {
        ThpaceGL.create(bgRef.current as HTMLCanvasElement, {
            // colors: ['#171923', '#44337a', '#521b41'],
            colors: ["#171923", "#702459", "#1a365d"],
            triangleSize: 100,
        })
    }, [])

    return (
        <Box as="section" minH="100vh">
            <canvas style={{position: "absolute", width: "100%", height: "100vh"}} ref={bgRef} />
            <HeroHeading navbarAnimControls={navbarAnimControls} />
        </Box>
    )
}

export default HeroSection